import React, { useState } from "react";

function EMICalc(){
    const [data,setData] = useState({
        amount:"", interset: "", month: ""
    });

    const [out,setOut] = useState(0);

    // here target values and collect it.

    const readValue = (e) => {
        const {name, value} = e.target;
        setData({...data, [name]: Number(value)})
    }

    // for emi calculator

    const submitHandler = (e) => {
        e.preventDefault();
        console.log(`data =`, data);
        
        let rate = (data.interset) / (12 * 100);
        
        console.log(`rate =`, rate)

        let x = (rate * Math.pow(1+rate,data.month)) / (Math.pow(1+rate,data.month) - 1)

        let output = data.amount * x;

        console.log(`output =`, output.toFixed(2));

        setOut(output.toFixed(2));
    }

    return(
        <div className="container">
            <div className="row">
                <div className="col-md-12 text-center">
                    <h3 className="display-3 text-secondary">EMI Calculator</h3>
                </div>
            </div>

            <div className="row">
                <div className="col-md-6 offset-md-3">
                    <div className="card">
                        <div className="card-body">
                            <form onSubmit={ submitHandler }>
                                <div className="form-group mt-2">
                                    <label htmlFor="amount">Amount</label>
                                    <input type="number" name="amount" id="amount" value={data.amount} onChange={readValue} className="form-control" placeholder="Amount" required/>
                                </div>
                                <div className="form-group mt-2">
                                    <label htmlFor="interset">Interest (in %)</label>
                                    <input type="number" name="interset" id="interset" value={data.interset} onChange={readValue} className="form-control" placeholder="Interest (in %)" required />
                                </div>
                                <div className="form-group mt-2">
                                    <label htmlFor="month">Month</label>
                                    <input type="number" name="month" id="month" value={data.month} onChange={readValue} className="form-control" placeholder="Months" required/>
                                </div>
                                <div className="form-group mt-2">
                                    <input type="submit" value="Calculate EMI"  className="btn btn-success"/>
                                </div>
                            </form>
                        </div>

                         {/* display result */}                        
                        <div className="card-footer">
                            <ul className="list-group">
                                <li className="list-group-item">
                                    <strong>Loan Amount</strong>
                                    <span className="float-end"> &#8377; {data.amount}</span>
                                </li>
                                <li className="list-group-item">
                                    <strong>Interset</strong>
                                    <span className="float-end">{data.interset} %</span>
                                </li>
                                <li className="list-group-item">
                                    <strong>Tenure(Months)</strong>
                                    <span className="float-end">{data.month}</span>
                                </li>
                                <li className="list-group-item">
                                    <strong>EMI</strong>
                                    <span className="float-end">{out}</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EMICalc;