import React, { useState } from "react";

function BMICalc(props){

    const [data,setData] = useState({
        weight : "",
        height : "",
    });

    const readValue = (event) =>{
        // console.log(`${event.target.name}:${event.target.value}`)
        const {name, value} = event.target;    // target the value here
        setData({...data, [name]:value})
    };

    const [status,setStats] = useState("");
    const [css,setCss] = useState("text-dark");
    const [out,setOut] = useState("")

    const submitHandler = (e) => {
        e.preventDefault();
        console.log(`output =`, data);
            let result =(Number(data.weight)/ (Number(data.height) * 0.01)).toFixed(2);
            console.log(`result =`, result)
            let key = Number(result)
            setOut(key)
            
                if (key < 18){
                    setStats("Underweight");
                    setCss("text-danger");
                }
                else if (key >= 18.0 && key <= 24.9){
                    setStats("Normal");
                    setCss("text-success");
                }
                else if (key >= 25.0 && key <= 29.9){
                    setStats("Overweight");
                    setCss("text-warning");
                }
                else if (key >= 30.0 && key <= 34.9){
                    setStats("Obasity class-1");
                    setCss("text-danger");
                }
                else if (key >= 35.0 && key <= 39.9){
                    setStats("Obasity class-2");
                    setCss("text-danger");
                }
                else if (key >= 40){
                    setStats("Obasity class-3");
                    setCss("text-danger");
                }
                else{
                    setStats("Check Your Input")
                    setCss("text-primary")
                }
    }

    return(
        <div className="container">
            <div className="row">
                <div className="col-md-12 text-center">
                    <h3 className="display-3 text-secondary">BMI Calculator</h3>
                </div>
            </div>

            <div className="row">
                <div className="col-md-6 offset-md-3">
                    <div className="card">
                        <div className="card-body">
                            <form action="" onSubmit={submitHandler}>
                                <div className="form-group mt-2 mb-2">
                                    <label htmlFor="weight" class="form-label">Weight (kg)</label>
                                    <input type="number" name="weight" id="weight" value={data.weight} onChange={readValue} className="form-control" placeholder="weight(cm)" required/>
                                </div>

                                <div className="form-group mt-2 mb-2">
                                    <label htmlFor="height" class="form-label">Height (cm)</label>
                                    <input type="number" name="height" id="height" value={data.height} onChange={readValue} className="form-control" placeholder="height(cm)" required/>
                                </div>

                                <div className="form-group mt-2 mb-2">
                                    <input type="submit" value="Calculate" className="btn btn-success"/>
                                </div>
                            </form>
                        </div>

                        <div className="card-footer">
                            
                            <h5> Result : <span className={ css }> { status } </span> 
                            
                                 BMI Value :  { out } </h5>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default BMICalc;
