import React, { useState } from "react";

export default function AgeCalc(){

    const [date,setDate] = useState(new Date().toLocaleDateString())
    const [age,setAge] = useState(0)

    const readValue = (e) =>{
        setDate(e.target.value)
    }

    const submitHandler = (e) =>{


        e.preventDefault();

        let birthMonth, birthDate, birthYear;

        const month = [31,28,31,30,31,30,31,31,30,31,30,31];

        // console.log(`user given date =`, date);

        // seprate date, month and year from user date using object creation
        let update = new Date(date);

        let birthDetails = {
            date1 : update.getDate(),
            month : update.getMonth()+1,
            year : update.getFullYear(),
        }
       
       // fetch current date

        let today = new Date();
        let currentdate = today.getDate();
        let currentmonth = today.getMonth()+1;
        let currentyear = today.getFullYear();

        if (
            birthDetails.year > currentyear || 
            (birthDetails.month > currentmonth && 
             birthDetails.year === currentyear) ||
            (birthDetails.date1 > currentdate && 
             birthDetails.month === currentmonth && birthDetails.year === 
             currentyear)
        ){
            alert('Not Born Yet');
            return;
        }else{
             // calculate year,
            birthYear = currentyear - birthDetails.year;
        }
        

        if (currentmonth >= birthDetails.month){
            birthMonth = currentmonth - birthDetails.month;
            // console.log('a')
        }
        else{
            birthYear--;
            birthMonth = 12 + currentmonth - birthDetails.month;
        }

        // check for date and month

        if(currentdate >= birthDetails.date1){
            birthDate = currentdate - birthDetails.date1;
            // console.log('c')
        }
        else{
            birthMonth--;
            let days = month[currentdate - 2];   
            console.log(days);
            birthDate = days + currentdate - birthDetails.date1;
            // console.log(birthDate);
            // console.log('d')

            if(birthMonth < 0){
                birthMonth = 11;
                birthYear--;
                // console.log('d')
            }
        }

        let res = birthYear + " " + "year" + " " + birthMonth + " " + "month" + " " + birthDate + " " + "days";
        
        setAge(res);
    }

    

    return(
       <div className="container">
           <div className="row">
               <div className="col-md-12 text-center">
                  <h3 className="display-3">Age Calculator</h3>
               </div>
           </div>

           <div className="row">
               <div className="col-md-6 offset-md-3">
                   <form onSubmit={ submitHandler }>
                       <div className="form-group mb-2 mt-2">
                           <label htmlFor="date">Select Birth Date</label>
                           <input type="date" name="date" id="date" className="form-control" onChange={readValue} value={date} required/>
                       </div>
                       <div className="form-group mb-2 mt-2">
                           <input type="submit" value="Calculate" className="btn btn-success" />
                       </div>
                   </form>

                   <div className="card-footer">
                            
                            <h5> Result : {age} </h5>
                        </div>
               </div>
           </div>
        </div>
    )
}