import React, { useState } from "react";
import './calc.css'

function  GradeCalc(){
    const [mark,setMark] = useState({
        eng:"", IT:"", bio:"", che:"", phy:"", math:""
    })
    const [out,setOut]= useState("")
    const [grade,setGrade] = useState("");
    const [css,setCSS] = useState("");

    function readValue(e) {
        const {name, value} = e.target;
        setMark({...mark, [name]: Number(value)})
    }
    

    function submitHandler(e) {
        e.preventDefault();
        let total = Number(mark.eng) + Number(mark.IT) + Number(mark.bio) + Number(mark.che) + Number(mark.phy) + Number(mark.math);
        
        let avg =(total/600) * 100;

        setOut(avg.toFixed(2));

        if(avg >= 80 && avg <=100){
                setGrade("A")
        }       setCSS("text-success");  
    }
    return(
        <div className="mainDiv">
            <div>
                <h2 className="titleName">Grade Calculator</h2>    
            </div>
            <form onSubmit={ submitHandler }>
                <label htmlFor="">English : </label>
                <input type="number" name="eng" id="eng" value={mark.eng} onChange={ readValue }/>
                <br /><br />
                <label htmlFor="">Info Tech : </label>
                <input type="number" name="IT" id="IT" value={mark.IT} onChange={ readValue }/>
                <br /><br />
                <label htmlFor="">Biology : </label>
                <input type="number" name="bio" id="bio" value={mark.bio} onChange={ readValue }/>
                <br /><br />
                <label htmlFor="">Chemistry : </label>
                <input type="number" name="che" id="che" value={mark.che} onChange={ readValue }/>
                <br /><br />
                <label htmlFor="">Physic : </label>
                <input type="number" name="phy" id="phy" value={mark.phy} onChange={ readValue }/>
                <br /><br />
                <label htmlFor="">Mathametics : </label>
                <input type="number" name="math" id="math" value={mark.math} onChange={ readValue }/>
                <br /><br />
                <input type="submit" value="Submit" />
            </form>
            <div className="resultCSS">
                Result : { out } <br /><br />
                <p className={ css }> Grade : { grade } </p>
            </div>
        </div>
    )
}

export default GradeCalc;